import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
  name: 'selected-classify-tabs',
  props: {
    selectedItem: {
      type: Array,
      default: []
    }
  },
  data () {
    return {
      swiperOption: {
        slidesPerView: 4,
        paginationClickable: true,
        spaceBetween: 5,
        freeMode: true
      }
    }
  },
  computed: {},
  components: {
    swiper,
    swiperSlide
  },
  watch: {},
  methods: {},
  created () {},
  mounted () {}
}



