
import { dateMixin } from 'common/js/mixin'
import { mapGetters, mapMutations } from 'vuex'
import dateFormat from 'vux/src/tools/date/format.js'

import VHeader from 'components/v-header/v-header'
import DateRange from 'base/date-range/date-range'
import ClassifyTabs from 'base/classify-tabs/classify-tabs.vue'

export default {
name: 'analysis-settings',
mixins: [dateMixin],
props: {
},
data () {
return {
showFlag: false,
startTime: dateFormat(Date.now() - 7 * 24 * 60 * 60 * 1000, 'YYYY-MM-DD'),
endTime: dateFormat(Date.now(), 'YYYY-MM-DD'),
selectedItems: []
}
},
computed: {
...mapGetters({
modifyItem: 'analysis/modifyItem'
})
},
components: {
VHeader,
DateRange,
ClassifyTabs
},
watch: {},
methods: {
...mapMutations({
clearModifyItem: 'analysis/CLEAR_MODIFY_ITEM'
}),
onModifiyItem (items) {
this.selectedItems = items
},
modifyPlan () {
if (!this.checkTime(this.startTime, this.endTime)) return
if (!this.checkItem(this.selectedItems)) return
const itemsObj = {
startTime: this.startTime,
endTime: this.endTime,
selectedItems: this.selectedItems
}
this.$emit('modifyPlan', itemsObj)
this._clearItem()
},
createPlan () {
if (!this.checkTime(this.startTime, this.endTime)) return
if (!this.checkItem(this.selectedItems)) return
const itemsObj = {
startTime: this.startTime,
endTime: this.endTime,
selectedItems: this.selectedItems
}
console.log(itemsObj)
this.$emit('createPlan', itemsObj)
this._clearItem()
},
checkItem (selectedItems) {
if (selectedItems.length < 2) {
this.$vux.toast.show({
text: '至少选择两个对比项目',
time: 1000,
type: 'warn'
})
return false
}
return true
},
onChangeDate ({ dateStartTime, dateEndTime }) {
this.startTime = dateStartTime
this.endTime = dateEndTime
},
onSelectedItem (items) {
this.$nextTick(() => {
this.selectedItems = items
})
},
show () {
this.$emit('show')

},
hide () {
this._clearItem()
this.$emit('hide')

},
getTitle (label) {
switch (label) {
case 'company': {
return '企业分析设置'
}
case 'product': {
return '产品分析设置'
}
case 'tenderProduct': {
return '产品招标分析设置'
}
case 'tenderCompany': {
return '企业招标分析设置'
}
default: {
return ''
}
}
},
_clearItem () {
this.clearModifyItem()
this.startTime = ''
this.endTime = ''
this.selectedItems = []
}
},
created () {
if (this.modifyItem.id) {
this.startTime = this.modifyItem.startPoTime.trim().slice(0, 10)
this.endTime = this.modifyItem.endPoTime.trim().slice(0, 10)
}
},
mounted () {
},
activated () {
}
}



